html {
  font-size: 62.5%;

  background: url("../assets/background.jpg");
  background-size: cover;
  background-position: 200px;
  background-attachment: fixed;
  background-color: rgba(0, 0, 0, 0.918);
  background-blend-mode: overlay;
}

body {
  font-family: "Barlow", sans-serif;
  font-size: 1.8rem;
  color: $greywhite;
}

.content {
  min-height: calc(100vh - 120px);
  padding-top: 20px;
}

html,
body {
  margin: 0;
}

a {
  color: $yellow;
  text-decoration: none;
  text-transform: uppercase;
}

h1,
h2,
h3,
h4 {
  font-family: "Iceberg", cursive;
  color: $yellow;
}

h1 {
  border: 7px solid $primaryWhite;
  border-radius: 2rem;
  font-size: 5rem;
  background-color: $black;
  padding: 15px 0;
  text-align: center;
  animation: flicker 5s infinite alternate;
}

.wrapper {
  margin: 0 auto;
  max-width: 1200px;
  width: 90%;
}

header {
  text-align: center;
}

article.introText {
  margin: 0 auto 40px;
  padding: 0 20px;

  img {
    border-radius: 50%;
  }
  p {
    text-align: center;
    color: $yellow;
    font-size: 2.8rem;
  }
}

p {
  font-size: 2.3rem;
}

button {
  transition: all ease 0.5s;
}
button:hover {
  background-color: $lightartificialgreen;
  border-color: $lightartificialgreen;
}
button.dropdownForm:hover {
  background-color: $lightartificialgreen;
  border-color: $lightartificialgreen;
}

.greeting {
  text-align: center;
  span {
    color: $yellow;
    font-weight: 700;
  }
}

[name="dropdownForm"] {
  display: flex;
  justify-content: center;
  flex-wrap: nowrap;
  margin-bottom: 50px;
}

[name="dropdownForm"] button {
  padding-left: 20px;
  padding-right: 20px;
  background-color: $yellow;
}

select {
  padding: 10px;
  margin-right: 10px;
  background-color: transparent;
  color: $yellow;
}

h1 {
  margin-top: 0;
}

footer {
  margin-top: 50px;
  margin-bottom: 20px;
  p {
    font-size: 1.6rem;
    color: $red;
    font-weight: bold;
  }

  a {
    text-transform: capitalize;
  }

  .wrapper {
    display: flex;
    padding: 10px;
    background-color: $black;
    justify-content: space-between;
    border: 5px solid $red;
    border-radius: 15px;
  }
}

@media (max-width: 720px) {
  [name="dropdownForm"] {
    justify-content: stretch;
    flex-direction: column;
    margin-bottom: 50px;
  }

  select {
    margin-bottom: 10px;
    margin-right: 0;
  }

  [name="dropdownForm"] button {
    padding: 10px;
  }

  footer {
    flex-wrap: wrap;
    justify-content: center;
    p {
      padding: 2px;
      margin: 0;
    }
  }
}

@keyframes flicker {
  0%,
  19%,
  21%,
  23%,
  25%,
  54%,
  56%,
  100% {
    text-shadow: -0.2rem -0.2rem 1rem $primaryAccent,
      0.2rem 0.2rem 1rem $primaryAccent, 0 0 2rem $headingSelection,
      0 0 4rem $headingSelection, 0 0 6rem $headingSelection,
      0 0 8rem $headingSelection, 0 0 10rem $headingSelection;
    box-shadow: 0 0 0.5rem $primaryWhite, inset 0 0 0.5rem $primaryWhite,
      0 0 2rem $primaryColor, inset 0 0 2rem $primaryColor,
      0 0 4rem $headingSelection, inset 0 0 4rem $primaryColor;
  }
  20%,
  24%,
  55% {
    text-shadow: none;
    box-shadow: none;
  }
}
